.header {
  font-family: 'Lora', serif;
  display: inline-block;
  position: relative;
  font-weight: 400;
  line-height: 1;
  color: black;
  font-size: 3rem;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
}

.headerContainer {
  position: relative;
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .header {
    border-top: 2px solid black;
    border-bottom: 2px solid black;
  }
}
